body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.spinner {
  width: 150px;
  height: 150px;
  position: relative;
  margin: 200px auto 170px auto;
}

.double-bounce1, .double-bounce2 {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #FF8C18;
  opacity: 0.6;
  position: absolute;
  top: 0;
  left: 0;
  
  -webkit-animation: sk-bounce 2.0s infinite ease-in-out;
  animation: sk-bounce 2.0s infinite ease-in-out;
}

.double-bounce2 {
  -webkit-animation-delay: -1.0s;
  animation-delay: -1.0s;
}

@-webkit-keyframes sk-bounce {
  0%, 100% { -webkit-transform: scale(0.0) }
  50% { -webkit-transform: scale(1.0) }
}

@keyframes sk-bounce {
  0%, 100% { 
    transform: scale(0.0);
    -webkit-transform: scale(0.0);
  } 50% { 
    transform: scale(1.0);
    -webkit-transform: scale(1.0);
  }
}
/*//////////////////////////////////////////////////////////////////
[ FONT ]*/

@font-face {
    font-family: OswaldLight;
    src: url(/Weather-forecast/static/media/Oswald-Light.0e85aec1.ttf); 
}

/*//////////////////////////////////////////////////////////////////*/

* {
    margin: 0px; 
    padding: 0px; 
    box-sizing: border-box;
    color: #333;
}

body, html {
    height: 100vh;
    font-family: sans-serif;
    background-color: #EDF8F9;
}

/* ------------------------------------ */
h1,h2,h3,h4,h5,h6 {margin: 0px;}

p {
    font-size: 16px;
    margin: 0px;
}

ul, li {
    margin: 0px;
    list-style-type: none;
}

/* ------------------------------------ */
input {
    outline: none;
    border: none;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 15px;
    margin-right: 10px;
    font-size: 18px;
    border-radius: 20px;
}

/* ------------------------------------ */
button {
    outline: none;
    border: solid 2px #fff;
    color: #fff;
    border-radius: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 15px;
    padding-right: 15px;
    margin-bottom: 15px;
    background: transparent;
    display: inline-block;
    text-align: center;
    transition: background-color .3s ease-in-out, border .3s ease-in-out;
}

button:hover, button:focus {
    cursor: pointer;
    background-color: #00697D;
    border: solid 2px transparent;
}

img {
    max-width: 100%;
    height: auto;
    max-height: 450px;
    margin-top: 25px;
    display: inline-block;
}

h1 {
    font-family: OswaldLight;
    font-size: 25px;
    padding-top: 10px;
    padding-bottom: 10px;
}

form {
    text-align: center;
}

.container-fluid {
    padding-right: 0px;
    padding-left: 0px;
    margin-right: auto;
    margin-left: auto;
}

.container {
    padding-right: 0px;
    padding-left: 0px;
    margin-right: auto;
    margin-left: auto;
    max-width: 100%;
}

.title-background {
    background-color: #353432;
    border-bottom: 1px solid #666;
}

.title-background h1 {
    color: #fff;
}

.header-background {
    background-color: #4E4D4A;
}

.header-background h3 {
    font-size: 35px;
    padding-top: 30px;
    padding-bottom: 10px;
    text-align: center;
    background-color: #4E4D4A;
    color: #FF8C18;
}

.header-background p {
    text-align: center;
}

.content {
    text-align: center;
    display: none;
}

.content-forecast {
    height: 45px;
    padding-top: 15px;
    padding-bottom: 15px;
    margin-top: 40px;
    background-color: #D3D3D3;
    text-align: center;
}

.weather-forecast {
    background-color: #4E4D4A;
}

.error-message {
    color: #ecf0f1;
    height: 20px;
    padding-bottom: 35px;
}

.spinning-pic-wrapper {
    text-align: center;
}

#spinningPic {
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
}

.nonVisible {
    display: none;
}

.visible {
    display: block;
}

.footer-background {
    background-color: #353432;
    border-top: 1px solid #666;
    font-family: OswaldLight;
    font-size: 18px;
    padding-top: 10px;
    padding-bottom: 10px;
    text-align: right;
}

.footer-background h3 {
    color: #fff;
    display: inline-block;
    margin-right: 10px;
}

.icon-wrapper {
    display: inline-block;
    height: 25px;
    width: 25px;
}

.icon-wrapper img{
    margin-top: 0px;
}

@media (min-width: 479px) {
    .container {
        width: 479px;
    }
}

@media (min-width: 768px) {
    .container {
        width: 750px;
    }
}

@media (min-width: 992px) {
    .container {
        width: 970px;
    }
}

@media (min-width: 1200px){
    .container {
        width: 1170px;
    }
}

@media screen and (max-width: 1252px) {

}

/*** Start of large tablet styles ***/
@media screen and (max-width: 991px) {
    .content-forecast {
        height: 80px;
        padding-top: 15px;
        padding-bottom: 15px;
        margin-top: 40px;
        background-color: #D3D3D3;
        text-align: center;
    }

    img {
        max-height: 300px;
    }
}

/*** Start of medium tablet styles ***/
@media screen and (max-width: 768px) {
    .container{
        padding-right: 10px;
        padding-left: 10px;
    }

    .header-background h3 {
        font-size: 35px;
    }
}

